.body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #b0e0e6;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.breed-select {
  width: 200px;
  justify-content: center;
  margin: 30px auto;
  display: flex;
}

img {
  border-radius: 10px;
}

h2 {
  margin: 50px;
}

.cat-info {
  width: 500px;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  margin-top: 5px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  box-shadow: 0 8px 26px #fbfafa, 0 28px 30px rgba(241, 7, 34, .3);
}

.temp-style {
  font-weight: 600;
}

.error {
  color: #ff3d00;
  font-weight: 600;
}

.is-hidden {
  display: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #ff3d00;
  border-color: #ff3d00 rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: 50px;
  animation: rotation 1s linear infinite;
  display: block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

